import { getComments } from "@/api/article";
export default {
  name: "ArticleComments",
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      comments: []
    };
  },
  async mounted() {
    // 不需要SEO， 放到mounted中即可
    const { data } = await getComments(this.article.slug);
    this.comments = data.comments;
    console.log(this.comments, "this.comments");
  }
};
